import {
  IntegrationsFlags,
  AnalyticsApi,
  InitializedSegmentClient,
} from "./types";
import noop from "utils/noop";

export const createSegmentAnalytics = (
  integrations: IntegrationsFlags,
  client: InitializedSegmentClient,
): AnalyticsApi => {
  return {
    sendTiming: noop,
    sendConsent(event) {
      client.track(event.type, { target: event.target }, { integrations });
    },
    trackEvent({ value, properties }) {
      client.track(value, properties, { integrations });
    },
  };
};

export const createGoogleAnalytics = (
  googleClientAsync: Promise<UniversalAnalytics.ga>,
): AnalyticsApi => {
  return {
    sendTiming(event) {
      googleClientAsync.then((client) =>
        client("send", {
          hitType: "timing",
          timingCategory: "Lifecycle",
          timingVar: event.type,
          timingValue: event.time,
        }),
      );
    },
    sendConsent: noop,
    trackEvent: noop,
  };
};

export const createCompositeAnalytics = (
  ...analytics: AnalyticsApi[]
): AnalyticsApi => {
  return {
    sendTiming(event) {
      analytics.forEach((a) => a.sendTiming(event));
    },
    sendConsent(event) {
      analytics.forEach((a) => a.sendConsent(event));
    },
    trackEvent(event) {
      analytics.forEach((a) => a.trackEvent(event));
    },
  };
};
