import {
  IntegrationsFlags,
  ApplicationDidLaunchEvent,
  ApplicationDidInitialRenderEvent,
  ComplianceTargets,
  ApplicationDidCheckCookieComplianceEvent,
  InitializedSegmentClient,
} from "./types";

let didLoadSegment = false;

export const initSegmentClientIfNeeded = (
  segmentKey: string | undefined,
  integrations: IntegrationsFlags,
  client?: SegmentAnalytics.AnalyticsJS,
): InitializedSegmentClient => {
  if (!segmentKey) throw new Error("Missing Segment key");
  if (!didLoadSegment) client && client.load(segmentKey, { integrations });
  return new InitializedSegmentClient(client);
};

/**
 * Since Google Analytics loads at some point after app launch we need to await it
 */
export const resolveGoogleAnalytics = (): Promise<UniversalAnalytics.ga> => {
  const waitAndResolve = (resolve: (ga: UniversalAnalytics.ga) => void) => {
    if (window.ga) return resolve(window.ga);
    else setTimeout(() => waitAndResolve(resolve), 250);
  };

  return new Promise(waitAndResolve);
};

export const createApplicationDidLaunchEvent = (
  elapsedTimeFromNavStart: number,
): ApplicationDidLaunchEvent => ({
  type: "ApplicationDidLaunch",
  time: elapsedTimeFromNavStart,
});

export const createApplicationDidInitialRenderEvent = (
  elapsedTimeFromNavStart: number,
): ApplicationDidInitialRenderEvent => ({
  type: "ApplicationDidInitialRender",
  time: elapsedTimeFromNavStart,
});

export const createApplicationDidCheckCookieComplianceEvent = (
  target: ComplianceTargets,
): ApplicationDidCheckCookieComplianceEvent => ({
  type: "Cookie Compliance Checked",
  target,
});
