import Cookies from "js-cookie";
import { getCookiesDomain } from "utils/getCookiesDomain";
import {
  LayoutAction,
  UpdateAnalyticsIntegrationsAction,
} from "contexts/Layout";
import { IntegrationsFlags } from "telemetry/types";
import { Dispatch } from "react";

export const marketingAttributionProperties = [
  "rc",
  "ref_code",
  "group_code",
  "keyword",
  "gclid",
  "ad_id",
  "adset_id",
  "campaign_id",
  "adgroup_id",
  "feeditem_id",
  "target_id",
  "loc_physical_ms",
  "loc_interest_ms",
  "matchtype",
  "network",
  "device",
  "creative",
  "placement",
];

export const getSavedMarketingAttributionProperties = (): {
  [key: string]: string;
} =>
  marketingAttributionProperties.reduce(
    (urlParameterValues, urlParameter) => ({
      ...urlParameterValues,
      [urlParameter]: sessionStorage.getItem(urlParameter) || undefined,
    }),
    {},
  );

// These are the default rejected cookie items for segment integrations
//segment.com/docs/connections/sources/catalog/libraries/website/javascript/#load-options

export const defaultRejectedCookiesItems: IntegrationsFlags = {
  "Amazon S3": true,
  Hotjar: true,
  "Google Tag Manager": true,
  AdWords: false,
  "Facebook Pixel": false,
  "LinkedIn Insight Tag": false,
  Mutiny: false,
  "Twitter Ads": false,
  Webhooks: true,
};

// Take default rejected and set all to true

export const allAcceptedCookiesItems = () => {
  Object.keys(defaultRejectedCookiesItems).forEach(
    (cookie) => (defaultRejectedCookiesItems[cookie] = true),
  );
  return defaultRejectedCookiesItems;
};

type CookieAction = "explicitlyDenied" | "softAccept" | "explicitlyAccepted";

export const setTrackingPreferenceCookies = (action: CookieAction): void => {
  let destinations = Object.assign({}, defaultRejectedCookiesItems);

  if (action !== "explicitlyDenied") {
    destinations = allAcceptedCookiesItems();
  }
  const domain = getCookiesDomain();

  Cookies.set(
    "tracking-preferences",
    { destinations, ccpaInteraction: action },
    {
      expires: 365,
      domain,
    },
  );
};

type LoadAnalyticsPref = "accepted" | "rejected";

export const loadAnalytics = (
  dispatch: Dispatch<LayoutAction>,
  trackingPreference: LoadAnalyticsPref,
): void => {
  let segmentPref = defaultRejectedCookiesItems;
  if (trackingPreference === "accepted") {
    segmentPref = allAcceptedCookiesItems();
  }

  dispatch(new UpdateAnalyticsIntegrationsAction(segmentPref));
};

export const getBannerInteraction = () => {
  const domain = getCookiesDomain();

  const trackingPreferences = Cookies.get("tracking-preferences", {
    domain,
  });

  if (trackingPreferences !== undefined) {
    return JSON.parse(trackingPreferences).ccpaInteraction;
  }
  return "noPreviousInteraction";
};
