import {
  createSegmentAnalytics,
  createGoogleAnalytics,
  createCompositeAnalytics,
} from "telemetry/analyticsApi";
import { defaultRejectedCookiesItems } from "utils/analytics";
import {
  initSegmentClientIfNeeded,
  resolveGoogleAnalytics,
} from "telemetry/analytics";
import { LayoutState } from "contexts/Layout";

/**
 * Compose all dependencies here as early in the app as possible
 */
export default function compositionRoot() {
  if (!process.env.SEGMENT_KEY) throw new Error("Missing segment key");

  const segmentClient = initSegmentClientIfNeeded(
    process.env.SEGMENT_KEY,
    defaultRejectedCookiesItems,
    window.analytics,
  );

  const segmentAnalyticsApi = createSegmentAnalytics(
    defaultRejectedCookiesItems,
    segmentClient,
  );

  const googleAnalyticsApi = createGoogleAnalytics(resolveGoogleAnalytics());
  const analyticsApi = createCompositeAnalytics(
    segmentAnalyticsApi,
    googleAnalyticsApi,
  );

  return {
    initialUIState: new LayoutState(defaultRejectedCookiesItems),
    segmentClient,
    analyticsApi,
    googleAnalyticsApi,
    analyticsIntegrations: defaultRejectedCookiesItems,
  };
}
