import React from "react";
import useModel, {
  AsyncWorkflow,
  AsyncWorkflowDispatch,
} from "hooks/useModelHook";
import {
  IntegrationsFlags,
  InitializedSegmentClient,
  AnalyticsApi,
} from "telemetry/types";
import {
  createSegmentAnalytics,
  createCompositeAnalytics,
} from "telemetry/analyticsApi";
import { defaultRejectedCookiesItems } from "utils/analytics";
import noop from "../utils/noop";

export class LayoutState {
  showCCPABanner = false;
  trackingPrefChecked = false;

  constructor(public analyticsIntegrations: IntegrationsFlags) { }
}

export enum LayoutActionType {
  ShowBanner,
  HideBanner,
  TrackBannerCheck,
  UpdateAnalyticsIntegrations,
}

export class ShowBannerAction {
  public readonly type = LayoutActionType.ShowBanner;
}

export class HideBannerAction {
  public readonly type = LayoutActionType.HideBanner;
}

export class TrackBannerCheckAction {
  public readonly type = LayoutActionType.TrackBannerCheck;
}

export class UpdateAnalyticsIntegrationsAction {
  public readonly type = LayoutActionType.UpdateAnalyticsIntegrations;
  constructor(public readonly payload: IntegrationsFlags) { }
}

export type LayoutWorkflow = AsyncWorkflow<LayoutAction, LayoutState>;

export type LayoutAction =
  | ShowBannerAction
  | HideBannerAction
  | TrackBannerCheckAction
  | UpdateAnalyticsIntegrationsAction;

export const layoutReducer: React.Reducer<LayoutState, LayoutAction> = (
  state: LayoutState,
  action: LayoutAction,
): LayoutState => {
  switch (action.type) {
    case LayoutActionType.HideBanner:
      return { ...state, showCCPABanner: false };
    case LayoutActionType.ShowBanner:
      return { ...state, showCCPABanner: true };
    case LayoutActionType.TrackBannerCheck:
      return { ...state, trackingPrefChecked: true };
    case LayoutActionType.UpdateAnalyticsIntegrations:
      return { ...state, analyticsIntegrations: action.payload };
    default:
      return state;
  }
};

interface ContextProps {
  initialState: LayoutState;
  segmentClient: InitializedSegmentClient;
  googleAnalyticsApi: AnalyticsApi;
}

interface LayoutContextValue {
  state: LayoutState;
  dispatch: AsyncWorkflowDispatch<LayoutState, LayoutAction>;
  analyticsApi: AnalyticsApi;
}

export const LayoutContext = React.createContext<LayoutContextValue>({
  state: new LayoutState(defaultRejectedCookiesItems),
  dispatch: noop,
  analyticsApi: createCompositeAnalytics(...[]),
});

export const LayoutContextProvider: React.FC<ContextProps> = ({
  initialState,
  segmentClient,
  googleAnalyticsApi,
  children,
}) => {

  if (typeof window !== `undefined`) {
    if (!initialState) throw new Error("Missing initial state");
    if (!segmentClient) throw new Error("Missing Segment client");
    if (!googleAnalyticsApi) throw new Error("Missing googleAnalyticsApi");
  }

  const [state, dispatch] = useModel(layoutReducer, initialState);
  const analyticsApi = createCompositeAnalytics(
    createSegmentAnalytics(state.analyticsIntegrations, segmentClient),
    googleAnalyticsApi,
  );

  let value = {
    state,
    dispatch,
    analyticsApi,
  };

  return (
    <LayoutContext.Provider value={value}>{children}</LayoutContext.Provider>
  );
};
