export type ApplicationDidLaunchEvent = {
  type: "ApplicationDidLaunch";
  time: number;
};

export type ApplicationDidInitialRenderEvent = {
  type: "ApplicationDidInitialRender";
  time: number;
};

export type TimingEvent =
  | ApplicationDidLaunchEvent
  | ApplicationDidInitialRenderEvent;

export type ComplianceTargets =
  | "CCPA.accepted"
  | "CCPA.rejected"
  | "noBannerRequired";

export type ApplicationDidCheckCookieComplianceEvent = {
  type: "Cookie Compliance Checked";
  target: ComplianceTargets;
};

export type AnalyticsEvent = {
  value: string;
  properties: Object;
};

export type ConsentEvent = ApplicationDidCheckCookieComplianceEvent;

export type AnalyticsApi = {
  sendTiming(event: TimingEvent): void;
  sendConsent(event: ConsentEvent): void;
  trackEvent(event: AnalyticsEvent): void;
};

export class InitializedSegmentClient {
  // Ignoring because tag is just there as a brand
  // @ts-ignore
  private tag = "InitializedSegmentClient";
  constructor(public value?: SegmentAnalytics.AnalyticsJS) {}

  track(
    type: string,
    properties: object,
    integrations: SegmentAnalytics.SegmentOpts,
  ) {
    if (!this.value) return;
    this.value.track(type, properties, integrations);
  }
}

export type IntegrationsFlags = {
  "Amazon S3": boolean;
  Hotjar: boolean;
  "Google Tag Manager": boolean;
  AdWords: boolean;
  "Facebook Pixel": boolean;
  "LinkedIn Insight Tag": boolean;
  Mutiny: boolean;
  "Twitter Ads": boolean;
  Webhooks: boolean;
};
